"use client";

import * as React from "react";
import Image from "next/image";
import Link from "next/link";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel";

interface Article {
  id: number;
  title: string;
  slug: string;
  image: string;
  content?: string;
}

/**
 * Safely resolve image URLs coming from backend or Cloudinary.
 */
function getImageUrl(path?: string): string {
  if (!path) return "/fallback-image.png"; // ✅ Fallback image

  // Already a full URL (Cloudinary, S3, etc.)
  if (path.startsWith("http")) return path;

  // Cloudinary shorthand like "image/upload/articles/30gold.webp"
  if (path.startsWith("image/")) {
    return `https://res.cloudinary.com/dvksqgurb/${path}`;
  }

  // Handle backend-served paths like "/media/articles/..."
  const baseUrl = process.env.NEXT_PUBLIC_API_URL?.replace(/\/$/, "") || "";
  const cleanPath = path.startsWith("/") ? path : `/${path}`;
  return `${baseUrl}${cleanPath}`;
}

export default function FeaturedCarousel() {
  const [articles, setArticles] = React.useState<Article[]>([]);

  React.useEffect(() => {
    async function fetchFeatured() {
      try {
        const res = await fetch(
          `${process.env.NEXT_PUBLIC_API_URL}/api/events/events/`
        );
        if (!res.ok) throw new Error(`HTTP ${res.status}`);
        const data = await res.json();
        console.log("✅ Featured Articles:", data);
        setArticles(data);
      } catch (err) {
        console.error("❌ Error loading featured articles:", err);
      }
    }

    fetchFeatured();
  }, []);

  const truncateText = (text: string, length: number) => {
    if (!text) return "";
    return text.length > length ? text.slice(0, length) + "..." : text;
  };

  return (
    <div className="w-full py-4">
      <Carousel className="w-full">
        <CarouselContent>
          {articles.map((article) => (
            <CarouselItem key={article.id} className="basis-full relative">
              <Link href={`/articles/${article.slug}`} className="block relative">
                <div className="relative w-full h-96 mb-6">
                  <Image
                    src={getImageUrl(article.image)}
                    alt={article.title}
                    fill
                    className="object-cover rounded-lg"
                    unoptimized // ✅ disable Next.js optimizer (prevents 400 errors)
                  />
                </div>

                <div className="absolute bottom-0 left-0 w-full bg-black/50 text-white p-4 rounded-b-xl">
                  <h2 className="text-lg sm:text-2xl font-bold">{article.title}</h2>
                  <p className="text-sm opacity-90">
                    {truncateText(article.content || "", 500)}
                  </p>
                </div>
              </Link>
            </CarouselItem>
          ))}
        </CarouselContent>
        <CarouselPrevious />
        <CarouselNext />
      </Carousel>
    </div>
  );
}
